* {
  margin: 0;
  padding: 0;
  font-family: "Nunito Sans";
  color: white;
  text-decoration: none;
}


::-webkit-scrollbar {
  width: 0px;
  height: 0px;
}



.App {
  display: flex;
  padding: 1cm;
  justify-content: space-between;
  align-items: flex-start;
  gap: .5cm;
  height: calc(100vh - 2cm);
  background-color: #000;
  overflow: auto;
  // position: relative;


  // &::before{
  //   position: absolute;
  //   content: "";
  //   inset: 0;
  //   background-image: url("back.jpg");
  //   background-position: center;
  //   background-repeat: no-repeat;
  //   background-size: cover;
  // }

}

.AppRight {
  // border: 1px solid white;
  width: 100%;
  overflow: auto;
  height: 100%;
}

.AppLeft {
  // border: 1px solid white;
  width: 10cm;
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.setting-box {
  display: flex;
  justify-content: flex-end;
  gap: .3cm;
}

.setting-img {
  width: .8cm;
  height: .8cm;
  padding: .2cm;
  border-radius: 10px;
  transition: .3s;
  // border: 1px solid white;
  background-color: #1a1a1a;

  cursor: pointer;

  &:hover {

    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;

  }

}

.setting {
  width: 100%;
  height: 100%;
  transition: .25s all ease;
  // opacity: .7;
}

.settings-box {
  display: flex;
  align-items: flex-start;
  gap: .3cm;
}

.back-images {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: .3cm;
  flex-wrap: wrap;
  overflow-y: auto;
  max-height: 10.5cm;

}

.bimgbox {
  width: 5cm;
}

.bimg {
  width: 100%;
  filter: brightness(90%);
  cursor: pointer;
  transition: .2s;

  &:hover {
    filter: (brightness(100%));
  }
}

.settings-box-right {
  width: 100%;
}

.settings-box-left {
  // border-right: 1px solid rgb(53, 53, 53);
  display: flex;
  flex-direction: column;
  gap: .1cm;
  height: 100%;
}

.feature {
  padding: .3cm;
  cursor: pointer;
  border-radius: 10px;
  // border-bottom: 1px solid rgb(53, 53, 53);
  transition: .3s;

  &:hover {
    background-color: rgb(48, 48, 48);
  }
}

.activeFeature {
  background-color: rgb(48, 48, 48);
}

.time-text {
  color: rgb(238, 238, 238);
  font-size: 50px;
  font-family: "Nunito Sans";
  font-weight: 600;
}

.day-name {
  color: white;
}

.fav-box {

  display: flex;
  flex-wrap: wrap;

  gap: .4cm;
  // padding: 0cm .5cm;
  width: 11.6cm;
  // border: 1px solid white;

}

.fav {
  // border: 1px solid white;
  width: 2cm;
  height: auto;
  display: flex;

  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: .15cm;
  cursor: pointer;


  // &:hover {
  //   .fav-img {
  //     opacity: 1;
  //   }

  // }


}


.penc {
  width: 1cm;
  height: 1cm;
  margin-left: 1.2cm;
  margin-top: -.35cm;
  display: none;
  position: fixed;
  z-index: 10;

  cursor: pointer;

}

.fav-show {
  display: flex;

  &:hover {
    .penc {
      display: flex;
    }
  }

}



.fav-img-box {
  border-radius: 10px;
  width: 1.7cm;
  height: 1.7cm;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: .1cm;
  background-color: #1a1a1a;
}

.fav-img {
  width: 60%;
  height: 60%;
  // opacity: .7;
  transition: .25s all ease;
}

.pi {
  width: 45%;
  height: auto;
}

.fav-name {
  font-size: 13px;
  text-align: center;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  -webkit-line-clamp: 1;
  text-overflow: ellipsis;
  word-break: break-all;
}


.add-favs {
  position: fixed;
  inset: 0;
  z-index: 1;
  // border: 1px solid white;
  display: flex;
  justify-content: center;
  padding: 1cm;
  backdrop-filter: blur(10px);
}

#add-favs,
#setting,
#edit-favs {
  display: none;
}

.add-favs-box {
  margin-top: 3cm;
  // border: 1px solid white;
  border-radius: 10px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  gap: .5cm;
  padding: .6cm 1cm 1cm;
  background-color: #1a1a1a;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

.sbox {
  width: 20cm;
  max-height: 11cm;


}


.sbox-checks {
  display: flex;
  flex-direction: column;
  gap: .3cm;
}


.drpbox {
  // border: 1px solid white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  gap: .3cm;
  padding: .3cm;
  border-radius: 10px;
  background-color: #272727;
  border: 1px solid transparent;
}

.dropbtn {
  color: white;


  font-size: 16px;
  border-radius: 5px 0px 0px 5px;

  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;


}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  transition: hover .3s;
  border-radius: 10px;


}

/* Dropdown Content (Hidden by Default) */
/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: block;

  position: absolute;
  max-height: 10cm;
  margin-top: .2cm;

  overflow-y: auto;


  transition: 0.3s ease;
  border-radius: 10px;
  background-color: #272727;
  width: 100%;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;


}

/* Show the dropdown menu on hover */

.note {
  font-size: 13px;
  color: rgb(151, 151, 151);
}

.search-box {
  // border: 1px solid white;
  display: flex;
  align-items: center;

  border-radius: 10px 10px 0px 0px;
}

/* Links inside the dropdown */
.drpcont {
  color: white;
  padding: .2cm .2cm;
  text-decoration: none;
  display: block;
  cursor: pointer;
  border: 1px solid transparent;
  border-bottom: 1px solid rgb(66, 66, 66);
  transition: .3s ease;
  font-size: 15px;


  &:hover {
    background-color: #464646;
  }

}

.searchType {
  background-color: transparent;
  border: 1px solid transparent;
  border-bottom: 1px solid rgb(66, 66, 66);
  cursor: text;
  width: calc(100% - 18px);
  border-radius: 10px 10px 0px 0px;

  &:hover {
    background-color: #272727;
  }

  &:focus {
    outline: none;
    border: 1px solid white;
  }
}


.da {
  width: .3cm;
  height: auto;
  transition: .3s ease;
}

.daOpen {
  transform: rotate(180deg);
}

.sbox-check {
  display: flex;
  gap: .3cm;
  // border: 1px solid white;
  border-radius: 10px;
  padding: .4cm .4cm;
  background-color: #272727;
  justify-content: space-between;
  align-items: center;
}


.add-fav-form {
  display: flex;
  flex-direction: column;
  gap: .3cm;
  width: 10cm;
}

.checkox {
  width: 22px;
  height: 22px;
  border-radius: 10px;
  cursor: pointer;
  accent-color: blueviolet;
}



.add-fav-inp {
  padding: .3cm;
  background-color: transparent;
  color: white;
  border-radius: 10px;
  font-size: 16px;
  outline: none;
  border: 2px solid #8b8b8b;

  &:focus {
    outline: none;
    border: 2px solid white;
  }

  &::placeholder {
    color: rgb(138, 138, 138);
  }
}

.add-favs-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: .3cm;
}

.sbox-check-text {
  // font-size: 18px;
}

.head-add {
  font-size: 18px;
}

.close {
  width: .45cm;
  height: .45cm;

  cursor: pointer;
  opacity: .7;
  transition: .25s ease;

  &:hover {
    opacity: 1;
  }


}

.add-fav-sub {
  padding: .3cm;
  // background-color: transparent;
  color: white;
  border-radius: 10px;
  outline: none;
  background-color: blueviolet;
  border: 2px solid transparent;
  cursor: pointer;
  font-size: 16px;
  transition: .3s all ease;

  &:hover {
    background-color: rgb(114, 18, 204);
  }


}


.period {
  font-size: 20px;
  margin-left: .07cm;
}

.city-input-box {
  display: flex;
  margin-top: .2cm;
  align-items: center;
  gap: .2cm;
}

.city-name {
  padding: .15cm;
  border-radius: 5px;
  background-color: transparent;
  border: 1px solid rgb(86, 86, 86);
  color: white;
}

.check {
  width: .6cm;
  border-radius: 5px;
  cursor: pointer;
  border: 1px solid rgb(86, 86, 86);
  padding: .1cm;
}

.time-box {
  padding: .7cm 1.2cm;
  background-color: #1a1a1a;
  // background-color: transparent;
  /* margin-top: 2cm; */
  backdrop-filter: blur(20px);
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.temp-line {
  display: flex;
  margin-top: .2cm;
  gap: .3cm;
  align-items: center;
}


.dbutton {
  background-color: red;

  &:hover {
    background-color: rgb(197, 10, 10);
  }
}


.AppLight {
  background-color: rgb(228, 228, 228);
}

.time-box-light,
.fav-img-box-light,
.setting-img-light,
.add-favs-box-light {
  background-color: white;
}

.day-name-light,
.time-text-light,
.period-light,
.fav-name-light,
.head-add-light,
.sbox-check-text-light,
.dropbtn-light {
  color: black;
}

.drpcont-light {
  color: black;
  border-bottom: 1px solid rgb(207, 207, 207);

  &:hover {
    background-color: rgb(199, 199, 199);

  }
}


.feature-light {
  color: black;
  // border-bottom: 1px solid rgb(207, 207, 207);

  &:hover {
    background-color: rgb(223, 223, 223);
  }
}

.activeFeature-light {
  background-color: rgb(223, 223, 223);
}

.searchType-light {
  &:focus {
    border: 1px solid black;
  }

  &:hover {
    background-color: rgb(226, 226, 226);
  }
}

.sbox-check-light,
.dropdown-light,
.drpbox-light,
.dropdown-content-light {
  background-color: rgb(226, 226, 226);
}

.add-fav-inp-light {
  border: 2px solid rgb(187, 187, 187);
  color: black;

  &::placeholder {
    color: rgb(163, 163, 163);
  }

  &:focus {
    outline: none;
    border: 2px solid black;
  }
}

.App-light {
  background-color: rgb(227, 227, 227);
}

.App-custom {
  display: flex;
  padding: 1cm;
  justify-content: space-between;
  align-items: flex-start;
  gap: .5cm;
  height: calc(100vh - 2cm);
  background-image: url("/images/pic1.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.customcss {
  backdrop-filter: blur(2000px);
  background-color: transparent;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

.remove-photo {
  background-color: red;
  border-radius: 10px;
  padding: .15cm .3cm;
  border: none;
  cursor: pointer;
  transition: .2s;
  font-size: 16px;

  &:hover{
    background-color: rgb(201, 8, 8);
  }
}

.head-right-set {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: .5cm;
 
}